import React,{useState,useEffect,useRef} from 'react';
import "./App.css";

      function App() {
        const [result,setresult]=useState("");
        const inputRef=useRef(null);
        useEffect(()=>inputRef.current.focus()); 
        
        function handleclick(e){
          e.preventDefault();
            setresult(result.concat(e.target.name));
        }
        function backspace(){
          setresult(result.slice(0,result.length-1))
        }
        function clear(){
          setresult("")
        }
        function calculate(){
          try{
            setresult(eval(result).toString());
          }
          catch(error){
            setresult("Error");
          }
        }

    return(
        <div className="centercaly">
            <form name="calcy">
            
            <input type="text" name="answers" id="display_answer" value={result} ref={inputRef} />

            <div className="layer1">
            <button className="oper" id="" >MC</button>
            <button className="oper" id="" >MR</button>
            <button className="oper" id="">M+</button>
            <button className="oper" id="">M-</button>
            <button className="oper" id="">Ms</button>
            <button className="oper" id="">M</button>
            </div>
             
            <div className="layer2">
            <button className="oper" name="%" onClick={handleclick}><b>%</b></button>
            <button className="oper" id="clear" onClick={clear}><b>CE</b></button>
            <button className="oper" id="clear" onClick={clear}><b>C</b></button>
            <button className="oper" id="backspace" onClick={backspace}><b>âŒ«</b></button>
            </div>

            <div className="layer3">
            <button className="Main_num" name="7" onClick={(e) => handleclick (e)}><b>7</b></button>
            <button className="Main_num" name="8" onClick={(e) => handleclick (e)}><b>8</b></button>
            <button className="Main_num" name="9" onClick={(e) => handleclick (e)}><b>9</b></button> 
            <button className="oper" name="*" onClick={handleclick}><b>&times;</b></button>
            </div>

            <div className="layer3">
            <button className="Main_num" name="4" onClick={(e) => handleclick (e)}><b>4</b></button>
            <button className="Main_num" name="5" onClick={(e) => handleclick (e)}><b>5</b></button>
            <button className="Main_num" name="6" onClick={(e) => handleclick (e)}><b>6</b></button> 
            <button className="oper" name="-" onClick={handleclick}><b>-</b></button> 
            </div>

            <div className="layer3">
            <button className="Main_num" name="1" onClick={(e) => handleclick (e)}><b>1</b></button>
            <button className="Main_num" name="2" onClick={(e) => handleclick (e)}><b>2</b></button>
            <button className="Main_num" name="3" onClick={(e) => handleclick (e)}><b>3</b></button> 
            <button className="oper" name="+" onClick={handleclick}><b>+</b></button> 
            </div>

            <div className="layer3">
            <button className="oper" name="." onClick={(e) => handleclick (e)}><b>.</b></button>
            <button className="Main_num" name="0" onClick={(e) => handleclick (e)}><b>0</b></button>
            <button className="oper" name="/" onClick={(e) => handleclick (e)}><b>/</b></button>
            <button className="oper" id="result" onClick={calculate}>=</button>
            </div>
            </form>
        </div>
        
    );

}

export default App;